@import './colors';

@keyframes ToggleMobileNav {
    0% {
        transform: translateX(-100%);
        opacity: 0; 
    }
    100% {
        transform: translateX(0%); 
        opacity: 1; 
    }
}

@keyframes closeMobileNav {
    0% {
        transform: translateX(0); 
        opacity: 1; 
		
    }
    100% {
        transform: translateX(-100%); 
        opacity: 0; 
		
    }
}

@keyframes backgroundFade {
    0% {
        background-color: rgb(222, 214, 97);  
    }
    100% {
        background-color: $secondary-color; 
    }
}